import { Block, Controller } from '@ali/4ever-cangjie';
import { LIST_GROUP_TYPE } from '../constants';

export const isList = (node: Block) => !!node?.data?.list;

/**
 * 判断当前列表是否开启折叠模式
 * @param
 */
export function isListGroupAvailable(controller: Controller): boolean {
  return controller?.query('isListGroupAvailable');
}

/**
 * 判断是否为 listGroup 中的折叠节点
 * @param
 */
export function isFoldGroupNode(
  node: Block,
  controller: Controller,
) {
  if (!isList(node)) {
    return false;
  }
  // 列表未开启折叠模式
  if (!isListGroupAvailable(controller)) {
    return false;
  }
  const { document } = controller.value;
  const { level } = node.data.list;
  const nextNode = document.getNextBlock(node.key);
  const nextLevel = nextNode?.data?.list?.level;
  return nextLevel > level;
}

/**
 * 判断节点是否在 listGroup 中
 * @param
 */
export function isInListGroup(
  node: Block,
  controller: Controller,
): boolean {
  if (!isList(node) || !controller) {
    return false;
  }
  const { document } = controller.value;
  const { level } = node.data.list;

  if (!isListGroupAvailable(controller)) {
    return false;
  }
  if (isFoldGroupNode(node, controller)) {
    return true;
  }
  let previousNode = document.getPreviousBlock(node.key);

  while (previousNode && isList(previousNode)) {
    const prevLevel = previousNode?.data.list.level;
    if (prevLevel < level) {
      return true;
    }
    previousNode = document.getPreviousBlock(previousNode?.key);
  }
  return false;
}

/**
 * 判断是否为 嵌套的 listGroup 中第一个折叠节点
 */
export function isFirstFoldNode(node: Block, controller: Controller) {
  if (!isFoldGroupNode(node, controller)) {
    return false;
  }
  const { document } = controller.value;
  const { level } = node.data.list;

  let previousNode = document.getPreviousBlock(node.key);

  while (previousNode && isList(previousNode)) {
    const prevLevel = previousNode?.data.list.level;
    if (prevLevel < level) {
      return false;
    }
    previousNode = document.getPreviousBlock(previousNode?.key);
  }
  return true;
}

/**
 * 判断是否为 listGroup 中的普通节点
 * @param
 */
export function isNormalGroupNode(node: Block, controller: Controller) : boolean {
  return isList(node) && !isFoldGroupNode(node, controller);
}


export function isSameGroupFrom(group: Block, node: Block): node is Block {
  return !(group?.type === LIST_GROUP_TYPE && (group?.data?.list?.level >= node?.data?.list?.level));
}
